:"mb-2"},[_vm._v("Estructura:")]),_c('p',[_vm._v("Son preferibles los suelos con gran porcentaje de macroporos, determinados por el contenido de materia orgánica y en los cuales se permite la fluidez al movimiento del agua y del oxígeno que debe estar presente en la zona radical en una concentración suficiente para que se realicen los procesos de absorción de agua y nutrientes, así como la respiración y el desarrollo de nuevas raíces. ")])]),_c('div',{staticClass:"col-md-8 col-xl-4 mb-3"},[_c('figure',[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/temas/tema1/img_6.png"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"tarjeta bg-01 p-2"},[_c('div',{staticClass:"row ordenar_tarjeta justify-content-center align-items-center my-lg-4 p-3"},[_c('div',{staticClass:"col-md-12 col-xl-5 mb-4"},[_c('h3',{staticClass:"mb-2"},[_vm._v("Profundidad efectiva:")]),_c('p',[_vm._v("Determina el desarrollo y la profundidad de las raíces, se recomienda sembrar en suelos cuya profundidad efectiva supere los 50 cm. Además, se debe evitar capas de roca o arcillas endurecidas. ")])]),_c('div',{staticClass:"col-md-8 col-xl-7 mb-3"},[_c('figure',[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/temas/tema1/img_7.png"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"tarjeta bg-01 p-2"},[_c('div',{staticClass:"row ordenar_tarjeta justify-content-center align-items-center my-lg-4 p-3"},[_c('div',{staticClass:"col-md-12 col-xl-6 mb-4"},[_c('h3',{staticClass:"mb-2"},[_vm._v("Drenaje:")]),_c('p',[_vm._v("No se debe sembrar plantaciones de aguacate en terrenos con problemas de drenaje, tanto externo como interno, donde se pueda presentar la muerte de raíces y/o la aparición de enfermedades fungosas. El nivel freático debe ser superior a un metro de profundidad. ")])]),_c('div',{staticClass:"col-md-8 col-xl-6 mb-3"},[_c('figure',[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/temas/tema1/img_8.png"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"tarjeta bg-01 p-2"},[_c('div',{staticClass:"row ordenar_tarjeta justify-content-center align-items-center my-lg-4 p-3"},[_c('div',{staticClass:"col-md-12 col-xl-7 mb-4"},[_c('h3',{staticClass:"mb-2"},[_vm._v("pH:")]),_c('p',[_vm._v("Este puede afectar la disponibilidad y absorción de los nutrientes para la planta. El sistema radical absorbe los nutrientes que se encuentran disueltos en el agua del suelo. Valores extremos de pH, ya sea por acidez o alcalinidad, pueden provocar la precipitación de ciertos elementos nutritivos químicos, los cuales permanecen en forma no disponible para ser absorbidos por las raíces. ")])]),_c('div',{staticClass:"col-md-8 col-xl-5 mb-3"},[_c('figure',[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/temas/tema1/img_9.png"),"alt":"Texto que describa la imagen"}})])])])]),_c('div',{staticClass:"tarjeta bg-01 p-2"},[_c('div',{staticClass:"row ordenar_tarjeta justify-content-center align-items-center my-lg-4 p-3"},[_c('div',{staticClass:"col-md-12 col-xl-8 mb-4"},[_c('h3',{staticClass:"mb-2"},[_vm._v("Fertilidad:")]),_c('p',[_vm._v("es la capacidad de un suelo para suministrar todos y cada uno de los nutrientes que necesitan las plantas en el momento, cantidad y forma adecuados; para profundizar consultar documento denominado Diagnóstico visual de trastornos nutrimentales que se encuentra en el botón Materiales del programa / Materiales de apoyo / Documentos complementarios / Documentos complementarios: Actividad de aprendizaje.")])]),_c('div',{staticClass:"col-md-8 col-xl-4 mb-3"},[_c('figure',[_c('img',{staticClass:"mx-auto",attrs:{"src":require("@/assets/curso/temas/tema1/img_10.png"),"alt":"Texto que describa la imagen"}})])])])])])],1)]),_vm._m(9)]),_c('Separador'),_vm._m(10),_c('Separador'),_vm._m(11),_vm._m(12),_vm._m(13),_c('Separador'),_vm._m(14),_vm._m(15),_vm._m(16),_c('Separador'),_c('div',{staticClass:"row justify-content-center align-items-center mb-1"},[_vm._m(17),_vm._m(18),_c('div',{staticClass:"col-lg-10 mb-2"},[_c('div',{staticClass:"bg--img_03"},[_c('div',{staticClass:"row justify-content-center align-items-center mb-2"},[_vm._m(19),_c('div',{staticClass:"col-lg-8 mb-3"},[_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"a","clase-tarjeta":"tarjeta acordeonA"}},[_c('div',{staticClass:"row",attrs:{"titulo":"El nitrógeno (N):"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Es crucial para las funciones de las plantas, especialmente en la síntesis de proteínas que promueven el crecimiento. Su aplicación afecta la producción de hojas, el diámetro del tronco y el tamaño de los frutos, además de ser un componente de la clorofila, responsable del color verde y la fotosíntesis. Sin embargo, un exceso de nitrógeno puede causar un aumento desproporcionado de hojas, retrasar la formación de frutos, debilitar el sistema radicular y reducir la firmeza y contenido de aceite de los frutos, haciendo que los tejidos sean más susceptibles a plagas. Por otro lado, la deficiencia de nitrógeno resulta en frutos pequeños con epidermis verde claro y hojas cloróticas.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Fósforo (P):"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("El P es requerido en cantidades menores, frente a las dosis de nitrógeno (N) y potasio (K). Este elemento es indispensable en puntos de crecimiento activo de la planta puesto que estimula el desarrollo de raíces, formación de órganos de reproducción y hace parte del sistema de transporte de energía. El exceso puede acelerar la maduración y causar desbalances con otros elementos. La deficiencia genera la formación de frutos pequeños con epidermis verde claro. Es recomendable realizar aplicaciones de fósforo en la siembra y primeros meses de desarrollo de la plantación.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"El potasio (K):"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Es esencial para el crecimiento saludable de las plantas, participando en la fotosíntesis y en la utilización de energía solar, lo que lo hace crucial en condiciones nubladas. Regula el contenido de agua en las hojas, siendo vital durante sequías, y facilita el transporte de azúcares a los frutos, afectando su tamaño y calidad. Además, mejora la tolerancia de las plantas a plagas y enfermedades, y les ayuda a resistir el estrés. Sin embargo, un exceso puede causar desórdenes fisiológicos, mientras que su deficiencia produce frutos pequeños y vulnerables a la deshidratación. El aguacate es especialmente sensible a la salinidad, por lo que se sugiere utilizar diversas fuentes de potasio en lugar de solo cloruro de potasio (KCl).")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Calcio (Ca):"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Este elemento da estabilidad a la planta, formado estructuras rígidas de soporte, además de protección al ingreso de insectos plagas y enfermedades en el árbol. Es determinante en la estabilidad y calidad del fruto. El exceso puede generar desbalances y deficiencias de otros elementos como el magnesio (Mg) y el potasio (K). La deficiencia se observa en la detención del crecimiento, las hojas se enroscan y empiezan a secarse por las puntas y bordes, y se incrementa la susceptibilidad a pudriciones.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Magnesio (Mg):"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Hace parte principal de la molécula de clorofila, de allí su importancia en la fotosíntesis (proceso en el cual la planta produce azúcares para su crecimiento). Es fundamental en el proceso de floración y fructificación.")])])]),_c('div',{staticClass:"row",attrs:{"titulo":"Elementos menores:"}},[_c('div',{staticClass:"col-12"},[_c('p',{staticClass:"mb-0"},[_vm._v("Entre estos se encuentran el Hierro (Fe), Cobre (Cu), Manganeso (Mn), Molibdeno (Mo), Boro (B), Zinc (Zn) y Cloro (Cl). Son indispensables en el crecimiento y desarrollo de la planta, participando en procesos como la respiración, síntesis de clorofila, fotosíntesis, protección en condiciones de estrés, síntesis de hormonas, floración y transpiración.")])])])])],1)])])]),_vm._m(20),_vm._m(21),_vm._m(22),_vm._m(23),_vm._m(24)]),_c('Separador'),_vm._m(25),_c('div',{staticClass:"row justify-content-center align-items-center mb-3 ordenar_tarjeta_lg"},[_vm._m(26),_c('div',{staticClass:"col-lg-12 col-xl-8 mb-2"},[_c('SlyderF',{staticClass:"custom mb-5",attrs:{"columnas":"col-lg-6 col-xl-6"}},[_c('div',{staticClass:"tarjeta tarjeta--slyder p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-8 bg-04"},[_c('h4',{staticClass:"p-1 text-white text-center"},[_vm._v("Cucarrón marceño o chiza (Phyllophaga obsoleta):")])])]),_c('p',{staticClass:"text-center mb-0"},[_vm._v("El cucarrón marceño, presente entre 2100 y 2550 msnm, aparece en marzo y abril. Sus larvas son blancas, mientras que los adultos son escarabajos cafés, de 2,1 cm. Se alimentan de hojas tiernas, causando daño durante la floración y fructificación, afectando la corteza de frutos pequeños y las raíces. Para controlar su población, se recomiendan trampas de luz y trampas de mechón con ACPM. También se sugiere aplicar Bacillus popilliae al suelo o un hongo específico para mitigar su impacto en las plantas.")])]),_c('div',{staticClass:"tarjeta tarjeta--slyder p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-8 bg-04"},[_c('h4',{staticClass:"p-1 text-white text-center"},[_vm._v("Metarhizium anisopliae.")])])]),_c('p',{staticClass:"text-center mb-0"},[_vm._v("Taladrador del tronco y ramas (Copturomímus perseae): es un insecto pequeño de medio centímetro aproximadamente, color gris, con manchas ovaladas negras sobre las alas, su ciclo de vida va de 75 a 94 días")])]),_c('div',{staticClass:"tarjeta tarjeta--slyder p-4"},[_c('div',{staticClass:"row justify-content-center mb-3"},[_c('div',{staticClass:"col-8 bg-04"},[_c('h4',{staticClass:"p-1 text-white text-center"},[_vm._v("Las larvas y pupas")])])]),_c('p',{staticClass:"text-center mb-0"},[_vm._v("pasan todo su ciclo en el interior de ramas y tallos. Esta especie agujera el tronco y las ramas; las cuales se van secando hasta provocar la muerte del árbol. El ataque se manifiesta por la presencia de aserrín blanco fuera del orificio que producen en las ramas. Es la plaga que más daños causa en Colombia, considerada como la más importante del cultivo. Cuando la plaga se presenta, se debe manejar mediante la poda de las ramas afectadas, las cuales deben ser quemadas; después se debe aplicar en los cortes una pasta cicatrizante.")])])])],1)]),_c('div',{staticClass:"row justify-content-center align-items-center mb-3"},[_vm._m(27),_vm._m(28),_c('div',{staticClass:"col-lg-8 mb-3"},[_c('LineaTiempoD',{staticClass:"color-primario text-small"},[_c('div',{staticClass:"row",attrs:{"numero":"1","titulo":"Ácaros o arañitas, arañitas Tetranychus urticae, Tetranychus bimaculatus,Mononychellus planki, Oligonychus punicae y Oligonychus yothersi, Eriophyes sp:"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Los ácaros, insectos pequeños de menos de 1 cm, viven en colonias bajo las hojas y a lo largo de las nervaduras. Succionan la savia, causando manchas amarillentas y secado del follaje. En infestaciones severas, también afectan las yemas, que se deforman. Su incidencia es mayor en épocas secas y calurosas, y su población se regula por la lluvia y depredadores. Se recomienda el uso de acaricidas a base de azufre para manejar Oligonychus y Eriophyes.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"2","titulo":"Trips (Heliothrips haemorrhoidalis y Selenothrips rubrocinctus):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Los trips son insectos pequeños de 0.3 a 1.4 cm, con dos pares de alas largas, que pueden reproducirse sexual y asexualmente. Cada hembra pone alrededor de 60 huevos. Su desarrollo se ve afectado por humedades bajas, siendo más abundantes en épocas secas, cuando se hospedan en arvenses. Su ataque causa engrosamiento y agrietamiento en frutos y hojas, además de decoloración y caída de frutos pequeños. Se debe monitorear durante la floración y mantener las plantaciones libres de arvenses hospederas.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"3","titulo":"Escamas articuladas (Selenaspidus articulatus y Pseudo Ischnaspis bowery):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Son insectos planos, pequeños con cubierta de color y forma variable. Son plagas muy especializadas que chupan la savia de las plantas causando alteraciones en los tejidos vegetales, viven en colonias y atacan troncos, ramas, hojas y frutos. Su población se ve favorecida en época seca cuando disminuyen sus enemigos naturales.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"4","titulo":"Escamas protegidas (Saissetia oleae y Chrysomphalus dictyospermi):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Se conocen como escamas protegidas, debido a su consistencia dura. Son insectos pequeños, inmóviles y planos, cubiertos por un caparazón de cera, de diferentes formas y color, según la especie. En ataques fuertes pueden causar defoliación. Algunos de ellos secretan un líquido azucarado que atrae las hormigas y sobre el cual crece el hongo causante de la fumagina. Durante las épocas secas su población puede aumentar al verse disminuidos los enemigos naturales. Los primeros síntomas en las hojas son las manchas amarillentas en la superficie y los ataques muy fuertes pueden causar la defoliación de la planta. En los frutos causan manchas y a veces deformación. Ataca cultivos de aguacate que se encuentran descuidados, con mal manejo y abandonados")])])]),_c('div',{staticClass:"row",attrs:{"numero":"5","titulo":"Chinche Monalonion (Monalonion Dissimulatum):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Son insectos alargados de 1,5 cm de longitud y de color oscuro. Atacan principalmente las partes tiernas de los árboles, los daños son similares a quemaduras de cigarrillo que se confunde con hongos, los frutos pequeños detienen su crecimiento y se secan. Temperaturas y humedades relativas altas son muy favorables. El control se realiza con insecticidas de contacto categoría III y IV, y con buenas prácticas de manejo.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"6","titulo":"Gusano pega – pega o bicho candela (Platynota sp):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("La larva es de color verde claro y alcanza hasta 2 cm de longitud, raspa la superficie inferior de las hojas y producen una desecación. Pega las hojas entre sí y forma paquetes donde viven y completan su ciclo de desarrollo. Se presenta en mayor cantidad en períodos prolongados de sequía. El control se debe realizar de manera cultural, recogiendo los manojos de hojas y quemándolas; dada la forma como vive el insecto, el control químico no es eficaz.")])])])])],1),_c('div',{staticClass:"col-lg-8 mb-3"},[_c('LineaTiempoD',{staticClass:"color-primario text-small"},[_c('div',{staticClass:"row",attrs:{"numero":"7","titulo":"Gusano canasta (Oiketicus kirbyi):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("el daño de este insecto se distingue por la defoliación severa de cogollos, la cual está acompañada por la presencia de canastas alargadas en forma de tabaco, elaboradas por las larvas con trozos de hojas y filamentos sedosos. Es un gran comedor de hojas y sus daños son bastante notorios. El control biológico para este insecto tiene muy buenos resultados con la bacteria Bacillus thuringiensis.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"8","titulo":"Gusano comedor de hojas, Gusano barba de indio (Megalopyge orsíloco), Gusano pollo (Megalopyge lanata) y gusano perro del naranjo (Anchisiades papilio):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("el daño que pueden causar depende del número de individuos, todos son comedores de hoja. Tienen fácil control biológico con pájaros y avispas.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"9","titulo":"Vaquitas del follaje (Compsus sp):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Los adultos son insectos de color blanco perla que atacan cultivos en todas las zonas productoras. Su población aumenta sin podas, con alta densidad de siembra y durante la época de lluvias. Causan daños en hojas, flores y frutos, produciendo cortes irregulares en las hojas jóvenes. El manejo debe ser preventivo e incluir buenas prácticas, como podas, nutrición mineral y la aplicación de los hongos Beauveria bassiana y Metarhizium anisopliae al suelo o al follaje.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"10","titulo":"Hormiga arriera (Atta spp):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("las hormigas arrieras hacen cortes del follaje provocando la defoliación total o parcial del árbol. El control más efectivo para este insecto es el método mecánico, que consiste en la ubicación y eliminación de la reina cuando el hormiguero tiene un solo conducto, o la destrucción de las colonias, mediante labores normales como aradas y rastrilladas. La colocación de una barrera física al paso de las hormigas a través del tallo también es un método que presenta resultados favorables.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"11","titulo":"Perforador del fruto (Stenoma catenifer):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("Los adultos son polillas pequeñas con alas pardo amarillento que colocan sus huevos en frutos pequeños. Las larvas penetran la pulpa, formando galerías y dañando las semillas. Los frutos caen o pierden valor comercial tras el ataque. El control incluye buenas prácticas culturales, como fertilización, podas sanitarias y recolección de frutos afectados. Se recomienda aplicar insecticidas desde que el fruto cuaja, así como recoger, destruir y enterrar los frutos caídos para evitar infestaciones.")])])]),_c('div',{staticClass:"row",attrs:{"numero":"12","titulo":"Picudo del aguacate (Heilipus lauri y Heilipus pittieri):"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0"},[_c('p',[_vm._v("El cucarrón adulto es negro brillante con dos bandas amarillas y mide entre 5 y 15 mm. Perfora túneles en los frutos para depositar sus huevos; las larvas se alimentan de la semilla. Si el ataque ocurre en frutos pequeños, estos caen; en frutos maduros, pueden pudrirse por microorganismos. Además, el adulto se alimenta de brotes, hojas y frutos. Para el control, se recomienda recoger y enterrar los frutos caídos, aplicando insecticidas de baja toxicidad si es posible.")])])])])],1),_vm._m(29)]),_vm._m(30),_c('div',{staticClass:"row justify-content-center align-items-center mb-4"},[_vm._m(31),_c('div',{staticClass:"col-lg-12 mb-5"},[_c('div',{staticClass:"tarjeta fondo_1_2 p-4 slyder_1 bg-01"},[_c('SlyderA',{staticClass:"tarjeta--blanca",attrs:{"tipo":"b"}},[_c('div',{staticClass:"tarjeta bg-01 p-3"},[_c('div',{staticClass:"row justify-content-center align-items-center my-lg-4 p-1"},[_c('div',{staticClass:"col-md-12 col-xl-10 mb-4"},[_c('h3',{staticClass:"mb-2"},[_vm._v("Marchitez (Verticillium sp):")]),_c('p',[_vm._v("La marchitez por Verticillium es una enfermedad creciente que a menudo se confunde con la pudrición de raíces por P. cinnamomi. El hongo invade tallos y ramas, causando un crecimiento detenido y marchitez parcial o total, con hojas que se tornan marrones. Para controlarla, es fundamental ubicar los árboles en terrenos bien drenados y podar las ramas afectadas. Las medidas de manejo son similares a las de la pudrición radical, enfocándose en evitar el encharcamiento del suelo.")])])])]),_c('div',{staticClass:"tarjeta bg-01 p-2"},[_c('div',{s