as. Para su control, se recomienda el uso de preparados a base de azufre, aplicándolos antes y después de la floración, cada 8 a 15 días, según la intensidad del ataque.")])])])]),_c('div',{staticClass:"tarjeta bg-01 p-2"},[_c('div',{staticClass:"row ordenar_tarjeta justify-content-center align-items-center my-lg-4 p-3"},[_c('div',{staticClass:"col-md-12 col-xl-10 mb-4"},[_c('h3',{staticClass:"mb-2"},[_vm._v("Mancha negra o antracnosis (Colletotrichum gloeosporioides):")]),_c('p',[_vm._v("esta enfermedad ataca plantas en vivero produciendo muerte descendente y pudrición del injerto, en el campo afecta ramas produciendo muerte de cogollos. El hongo ocasiona pudrición de fruto en cosecha y poscosecha, deteriorando la calidad del fruto. ")])])])])])],1)]),_vm._m(32),_vm._m(33),_vm._m(34)]),_vm._m(35)],1)],1)
